import { getList } from './util'

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    namespace: 'data',
    state: {
        sizes: {
            'XS': true,
            'S': true,
            'M': true,
            'ML': true,
            'L': true,
            'XL': true,
            'XXL': true
        },              // ÂïÜÂìÅÂ∞∫Á†Å
        data: null,     // ÂïÜÂìÅÊï∞ÊçÆ
        shopCart: [],   // Ë¥≠Áâ©ËΩ¶
        isEject: false, // Ë¥≠Áâ©ËΩ¶ÊòØÂê¶ÂºπÂá∫
    },
    effects: {
        // Ëé∑ÂèñÂïÜÂìÅÊï∞ÊçÆ
        *getListHttp(_, { call, put }) {
            const result = yield call(getList)
            yield put({
                type: 'getData',
                payload: {
                    data: result.products
                }
            })
        },
        // Ê∑ªÂä†ÂïÜÂìÅ
        *addGood({ payload: item }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            if (tempArr.length !== 0) {
                let temp = null
                for (let i in tempArr) {
                    if (tempArr[i].id === item.id) {
                        tempArr[i].quantity++
                        temp = i
                        break
                    }
                }
                if (temp === null) {
                    item["quantity"] = 1
                    tempArr.push(item)
                }
            } else {
                item["quantity"] = 1
                tempArr.push(item)
            }
            yield put({
                type: 'addCart',
                payload: tempArr
            })
        },
        // Âà†Èô§ÂïÜÂìÅ
        *deleteCart({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    tempArr.splice(i, 1)
                    break
                }
            }
            yield put({
                type: 'deleteCarts',
                payload: tempArr
            })
        },
        // ÂáèÂ∞ë‰∏Ä‰ª∂
        *subtrackGood({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    if (tempArr[i].quantity - 1 === 0) {
                        tempArr.splice(i, 1)
                    } else {
                        tempArr[i].quantity--
                    }
                    break
                }
            }
            yield put({
                type: 'subtrack',
                payload: tempArr
            })
        },
        // Â¢ûÂä†‰∏Ä‰ª∂
        *addOneGood({ payload: id }, {_, put, select}) {
            const tempArr = yield select(state => {
                return JSON.parse(JSON.stringify(state.data.shopCart))
            })
            for (let i in tempArr) {
                if (tempArr[i].id === id) {
                    tempArr[i].quantity++
                    break
                }
            }
            yield put({
                type: 'add',
                payload: tempArr
            })
        },
    },
    reducers: {
        getData(state, { payload: data }) {
            // console.log('getData', state);
            return { ...state, ...data }
        },
        // Ëé∑ÂèñË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
        getShopCart(state, { payload: shopCart }) {
            // console.log(shopCart);
            return { ...state, shopCart }
        },
        get(state) {
            // console.log('get', state);
            return { ...state }
        },
        // ËøáÊª§Â∞∫Á†Å
        filterSize(state, { payload: sizes }) {
            // console.log('filterSize', sizes)
            return { ...state, ...sizes }
        },
        // ÊéíÂ∫è
        sort(state, { payload: data }) {
            // console.log('sort', data);
            return { ...state, ...data }
        },
        // Ê∑ªÂä†ÂïÜÂìÅ
        addCart(state, { payload: tempArr }) {
            // console.log('addCart', tempArr)
            return {
                ...state,
                shopCart: tempArr
            }
        },
        // Âà†Èô§ÂïÜÂìÅ
        deleteCarts(state, { payload: shopCart }) {
            // console.log('deleteCarts', shopCart);
            return { ...state, shopCart }
        },
        // ÂáèÂ∞ë‰∏Ä‰ª∂
        subtrack(state, { payload: shopCart }) {
            // console.log('subtrack', shopCart);
            return { ...state, shopCart }
        },
        // Ê∑ªÂä†‰∏Ä‰ª∂
        add(state, { payload: shopCart }) {
            // console.log('add', shopCart);
            return { ...state, shopCart }
        },
        // ÂºπÂá∫Ë¥≠Áâ©ËΩ¶
        eject(state) {
            // console.log('eject');
            return {
                ...state,
                isEject: true
            }
        },
        // ÂÖ≥Èó≠Ë¥≠Áâ©ËΩ¶
        closeShop(state) {
            // console.log('closeShop');
            return {
                ...state,
                isEject: false
            }
        }
    },
}