import React, { useState } from 'react'
import './index.css'
import { Menu, Dropdown } from 'antd'
import { connect } from 'dva'
import 'antd/dist/antd.css';
import Items from '../Items/index'

const mapStateToProps = state => {
    return {
        data: state.data.data,
        sizes: state.data.sizes
    }
}

function Shelf(props) {
    // Â≠òÂÇ®ÂΩìÂâçÂ±ïÁ§∫ÁöÑ‰∏™Êï∞
    const [quantity, setQuantity] = useState(0)
    // Â≠òÂÇ®ÊéíÂ∫èÊñπÂºè
    const [sortWay, setSortWay] = useState('ÁÇπÂáªÈÄâÊã©')

    // ÁÇπÂáªÊéíÂ∫è
    const sortItem = (e) => {
        // console.log(e);
        let obj = JSON.parse(JSON.stringify(props.data))
        if (e.key === '0') {
            obj.sort((a, b) => a.id - b.id)
            setSortWay('ÈªòËÆ§ÊéíÂ∫è')
        } else if (e.key === '1') {
            obj.sort((a, b) => a.price - b.price)
            setSortWay('‰ª∑Ê†º‰ªé‰ΩéÂà∞È´ò')
        } else if (e.key === '2') {
            obj.sort((a, b) => b.price - a.price)
            setSortWay('‰ª∑Ê†º‰ªéÈ´òÂà∞‰Ωé')
        }

        const { dispatch } = props
        dispatch({
            type: 'data/sort',
            payload: {
                data: obj
            }
        })
    }

    // ‰∏ãÊãâÁªÑ‰ª∂
    const menu = (
        <Menu onClick={(e) => sortItem(e)}>
            <Menu.Item key="0">ÈªòËÆ§ÊéíÂ∫è</Menu.Item>
            <Menu.Divider />
            <Menu.Item key="1">‰ª∑Ê†º‰ªé‰ΩéÂà∞È´ò</Menu.Item>
            <Menu.Divider />
            <Menu.Item key="2">‰ª∑Ê†º‰ªéÈ´òÂà∞‰Ωé</Menu.Item>
        </Menu>
    )

    const getNum = (len) => {
        setQuantity(len)
    }

    return (
        <div className="container">
            <div className="header">
                <span className="information">ÂÖ±Êü•ÊâæÂà∞ { quantity } ‰ª∂ÂïÜÂìÅ„ÄÇ</span>
                <div className="sort">
                    Ê†πÊçÆ&nbsp;&nbsp;
                    <Dropdown overlay={menu} trigger={['click']}>
                        <span className="select ant-dropdown-link" onClick={e => e.preventDefault()}> {sortWay} </span>
                    </Dropdown>
                    &nbsp;&nbsp;ÊéíÂ∫è
                </div>
            </div>
            <Items getNum={getNum} />
        </div>
    )
}

export default connect(mapStateToProps)(Shelf)
